.header{background-color:#4f2c2c;background-image:linear-gradient(to left, #4f2c2c, #6b3c3c);border-bottom:1px solid #ffb300;color:#fff;height:100%;display:flex;justify-content:space-between;align-items:center;flex-wrap:nowrap}.link-section{height:100%;display:flex;align-items:center}.link{font-size:1.3em;margin:0px 2px;padding:10px;text-align:center;white-space:nowrap;border-left:2px solid #472828}.header a{color:#e8a506}a:hover{color:#b34d09;background-color:#6b3c3c}.logo{margin:5px;width:67px;height:70px;background:url("../../images/9th_age_seal.png") no-repeat center;background-size:cover}.menu-section{display:none;width:100%;text-align:center}@media(max-width: 760px){.logo-section,.link-section{display:none}.menu-section{display:block}}