.hamburger-menu{display:grid;justify-items:center}.menu{position:fixed;top:0;left:0;z-index:1;width:100%;height:100%;background-color:#4f2c2c;background-image:linear-gradient(to left, #4f2c2c, #6b3c3c)}.menu.hide{display:none}.menu-items-wrapper{width:100%;height:200px;position:relative;border-bottom:1px solid #ffb300}.menu-items{position:absolute;top:0;bottom:0;left:0;right:-17px;overflow-y:scroll}.menu div:first{background-color:red}.menu-item{width:100%;height:50px;padding:8px;border-bottom:1px solid #472828;cursor:pointer;display:grid;align-items:center}.menu-item h5{font-size:1.2em}.menu-item:hover{background-color:#6b3c3c}div.cancel-icon{display:grid;justify-items:end;border-bottom:1px solid #472828}.hamburger-icon,svg.cancel-icon{width:50px;height:50px;cursor:pointer}.hamburger-icon{color:#e8a506}.cancel-icon{color:#b34d09}.cancel-icon-wrapper{align-self:end}