.layout{width:100%;height:100%;display:grid;grid-template-rows:90px auto;grid-template-areas:"header" "page_content"}.header{background-color:red;grid-area:header}.page-content-wrapper{width:100%;height:100%;overflow:hidden;position:relative;grid-area:"page_content"}.page-content{position:absolute;top:0;bottom:0;left:0;right:-17px;overflow-y:scroll}